.good_warper .tab {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  border-top: 0.01rem solid rgba(7, 17, 27, 0.1);
  border-bottom: 0.01rem solid rgba(7, 17, 27, 0.1);
}
.good_warper .tab .tab-item {
  -ms-flex: 1 1;
      flex: 1 1;
  display: block;
  position: relative;
  text-align: center;
  -webkit-transition-property: 'translateX', 'border';
  -o-transition-property: 'translateX', 'border';
  transition-property: 'translateX', 'border';
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  color: #4d555d;
  translateX: 0.28rem;
}
.good_warper .tab .tab-item.active {
  color: #f01414;
  font-size: 0.36rem;
}
.good_warper .tab .tab-item.active:before {
  content: '';
  position: absolute;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  border-bottom: 0.02rem solid #f01414;
  left: 0%;
  right: 0%;
  bottom: 0px;
}
.good_warper .tab .tab-item.active:active:before {
  left: 51%;
  right: 56%;
}



