.search {
  width: 100vw;
}
.search form {
  background-color: #fff;
  line-height: 0.5rem;
  height: 1rem;
  padding: 0rem 0.2rem;
}
.search form #search {
  width: 4.5rem;
  height: 0.5rem;
  border: 0.01rem solid #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #f4f4f4;
  border-radius: 0.06rem;
}
.search form button {
  width: 1.2rem;
  height: 0.52rem;
  margin-left: 0.2rem;
  border-radius: 0.06rem;
  color: #fff;
  background-color: #3190e8;
}



