.detail {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(7, 17, 27, 0.8);
}
.detail .c_warp {
  min-height: 100%;
  height: auto;
}
.detail .c_warp .c_c {
  margin-top: 1.28rem;
  padding-bottom: 1.28rem;
}
.detail .c_warp .c_c .name {
  line-height: 0.32rem;
  font-size: 0.32rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
}
.detail .c_warp .c_c .star-warp {
  margin-top: 0.32rem;
  margin-bottom: 0.56rem;
  text-align: center;
}
.detail .c_warp .c_c .info {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
.detail .c_warp .c_c .info p {
  font-weight: 700;
  position: relative;
  color: #ffffff;
  font-size: 0.32rem;
  margin-bottom: 0.48rem;
}
.detail .c_warp .c_c .info p:after {
  display: block;
  position: absolute;
  content: "";
  width: 40%;
  left: -0.4rem;
  bottom: 0.16rem;
  border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
}
.detail .c_warp .c_c .info p:before {
  display: block;
  position: absolute;
  bottom: 0;
  content: "";
  width: 40%;
  left: 68%;
  bottom: 0.16rem;
  border-bottom: 0.04rem solid rgba(255, 255, 255, 0.2);
}
.detail .c_warp .c_c .supports {
  width: 80%;
  margin: auto;
}
.detail .c_warp .c_c .supports .supports-item {
  padding: 0rem 0.24rem;
  margin-bottom: 0.24rem;
}
.detail .c_warp .c_c .supports .supports-item:last-child {
  margin-bottom: 0.56rem;
}
.detail .c_warp .c_c .supports .supports-item .icon {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  margin-right: 0.12rem;
  background-size: 0.32rem;
  background-repeat: no-repeat;
}
.detail .c_warp .c_c .supports .supports-item .icon.decrease {
  background-image: url("../../../images/decrease_2@2x.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
  .detail .c_warp .c_c .supports .supports-item .icon.decrease {
    background-image: url("../../../images/decrease_2@3x.png");
  }
}
.detail .c_warp .c_c .supports .supports-item .icon.discount {
  background-image: url("../../../images/discount_2@2x.png");
}
