  .detail .c_warp .c_c .supports .supports-item .icon.discount {
    background-image: url("../../../images/discount_2@3x.png");
  }
}
.detail .c_warp .c_c .supports .supports-item .icon.guarantee {
  background-image: url("../../../images/guarantee_2@2x.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
  .detail .c_warp .c_c .supports .supports-item .icon.guarantee {
