  }
}
.detail .c_warp .c_c .supports .supports-item .icon.invoice {
  background-image: url("../../../images/invoice_2@2x.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
  .detail .c_warp .c_c .supports .supports-item .icon.invoice {
    background-image: url("../../../images/invoice_2@3x.png");
  }
}
.detail .c_warp .c_c .supports .supports-item .icon.special {
  background-image: url("../../../images/special_2@2x.png");
}
@media screen and (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio) {
  .detail .c_warp .c_c .supports .supports-item .icon.special {
    background-image: url("../../../images/special_2@3x.png");
  }
}
.detail .c_warp .c_c .supports .supports-item .text {
  vertical-align: top;
  font-size: 0.24rem;
  color: #ffffff;
  line-height: 0.24rem;
}
.detail .c_warp .c_c .s_in {
  width: 80%;
  margin: auto;
  color: #ffffff;
  margin-top: 0.48rem;
  margin-bottom: 0.48rem;
  padding: 0rem 0.24rem;
  font-size: 0.24rem;
  line-height: 0.48rem;
  letter-spacing: 0.04rem;
}
.detail .detailColse {
  height: 1.28rem;
  margin-top: -1.28rem;
  margin-left: -0.24rem;
  left: 50%;
  position: absolute;
  clear: both;
  text-align: center;
}
.detail .detailColse .icon {
  font-size: 0.48rem;
  color: rgba(255, 255, 255, 0.5);
}



