.headTo {
  color: #fff;
  position: fixed;
  top: 0;
  width: 100VW;
  background-color: #3190e8;
  line-height: 0.8rem;
  height: 0.8rem;
  font-size: 0.28rem;
}
.headTo div {
  display: inline-block;
}
.headTo .h_l {
  position: absolute;
  top: 0rem;
  left: 0.1rem;
}
.headTo .h_l .iconfont {
  font-size: 0.4rem;
  vertical-align: middle;
}
.headTo .h_l .icon-dizhi {
  color: #11b9b2;
  vertical-align: top;
}
.headTo .h_l .site {
  display: inline-block;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 1.2rem;
  height: 0.32rem;
  line-height: 0.32rem;
  padding-left: 0.1rem;
}
.headTo .h_l a {
  color: #fff;
}
.headTo .h_c {
  text-align: center;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 4.8rem;
  padding: 0rem 0.8rem;
}
.headTo .h_c .search {
  position: relative;
  left: 0;
  width: auto;
}
.headTo .h_c .search i {
  color: #ccc;
  display: inline-block;
  position: absolute;
  line-height: 0.4rem;
  left: 0.1rem;
  top: 0.22rem;
}
.headTo .h_c .search .s_inp {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 0.4rem;
  width: 3rem;
  border-radius: 0.08rem;
  padding-left: 0.5rem;
}
.headTo .h_c a {
  color: #fff;
}
.headTo .h_r {
  position: absolute;
  top: 0rem;
  right: 0.1rem;
}
.headTo .h_r .r_l {
  padding-right: 0.1rem;
}
.headTo .h_r a {
  color: #fff;
}



