.rp {
  font-size: 0.28rem;
  width: 100%;
  background-color: #F5F5F5;
}
.rp .rp_main {
  padding-top: 1.2rem;
}
.rp .rp_main .f_warpper {
  line-height: 1rem;
  height: 1rem;
  padding: 0rem 0.2rem;
  border-bottom: 0.01rem solid #F5F5F5;
  color: #666;
  font-weight: 600;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
}
.rp .rp_main .f_warpper input {
  line-height: 0.8rem;
  padding-left: 0.15rem;
  caret-color: #ccc;
}
.rp .rp_main .f_warpper #code {
  width: 1.98rem;
  height: 0.4rem;
  border-right: 0.04rem solid #3dcc30;
}
.rp .rp_main .f_warpper .codeImg {
  line-height: 1.2rem;
  padding-left: 0.2rem;
  display: inline-block;
  vertical-align: top;
}
.rp .rp_main .f_warpper .codeImg img {
  width: 1.2rem;
  height: 0.5rem;
  margin-top: 0.3rem;
  display: inline-block;
}
.rp .rp_main .f_warpper .codeImg .codeText {
  width: 1.2rem;
  height: 0.5rem;
  vertical-align: top;
  margin-left: 0.2rem;
  display: inline-block;
}
.rp .rp_main .f_warpper .codeImg .codeText .toggle_img {
  line-height: 0.8rem;
  font-size: 0.28rem;
  display: inline-block;
  color: #3b95e9;
  font-weight: 200;
}
.rp .rp_main .sub {
  margin-top: 0.2rem;
  padding: 0rem 0.15rem;
}
.rp .rp_main .sub .submit {
  width: 6rem;
  height: 0.8rem;
  font-size: 0.28rem;
  border-radius: 0.08rem;
  display: inline-block;
  color: #fff;
  background-color: #4cd964;
}
.rp .rp_main .sub .disabled {
  width: 6rem;
  height: 0.8rem;
  font-size: 0.28rem;
  border-radius: 0.08rem;
  display: inline-block;
  color: #fff;
  background-color: #4cd964;
  background-color: #c4c4c4;
  color: #999;
}



