.ratingSelect {
  padding-top: 0.24rem;
}
.ratingSelect .ratingType {
  margin: 0rem 0.36rem;
  padding-bottom: 0.36rem;
  border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
}
.ratingSelect .ratingType .block {
  display: inline-block;
  color: #4d555d;
  background-color: #00a0dc;
  padding: 0.16rem 0.24rem;
  margin-right: 0.24rem;
  border-radius: 0.04rem;
  line-height: 0.32rem;
}
.ratingSelect .ratingType .block .text {
  font-weight: 700;
  font-size: 0.28rem;
}
.ratingSelect .ratingType .block .ratingSum {
  font-size: 0.16rem;
  margin-left: 0.1rem;
}
.ratingSelect .ratingType .block.active {
  color: #fff;
}
.ratingSelect .ratingType .block.goodPositive {
  background-color: rgba(0, 160, 220, 0.2);
}
.ratingSelect .ratingType .block.goodPositive.active {
  background-color: #00a0dc;
}
.ratingSelect .ratingType .block.goodNegative {
  background-color: rgba(77, 85, 93, 0.2);
}
.ratingSelect .ratingType .block.goodNegative.active {
  background-color: #4d555d;
}
.ratingSelect .switch {
  padding: 0.24rem 0.36rem;
  border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
}
.ratingSelect .switch .icon-gou {
  color: #93999f;
  vertical-align: middle;
  font-size: 0.24rem;
  line-height: 0.48rem;
}
.ratingSelect .switch .icon-gou.active {
  color: #00a0dc;
}
.ratingSelect .switch .text {
  font-size: 0.24rem;
  line-height: 0.48rem;
  margin-left: 0.08rem;
}



