.shop {
  position: fixed;
  overflow: hidden;
  left: 0;
  right: 0;
  top: 1.6rem;
  bottom: 0rem;
  padding: 0.2rem 0.3rem 0rem 0.3rem;
}
.shop .shopContent {
  position: relative;
  font-size: 0.3rem;
}
.shop .shopContent .shopItem {
  display: -ms-flexbox;
  display: flex;
  background-color: #fff;
  -webkit-box-shadow: 1px 2px 8px #333;
          box-shadow: 1px 2px 8px #333;
  padding: 0.15rem;
  border-radius: 0.08rem;
  margin-bottom: 0.4rem;
}
.shop .shopContent .shopItem .shopImg {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.2rem;
  border-radius: 0.08rem;
  overflow: hidden;
}
.shop .shopContent .shopItem .shopImg a {
  display: block;
}
.shop .shopContent .shopItem .shopImg img {
  width: 1.5rem;
  height: 1.5rem;
  background-color: red;
  visibility: hidden;
  vertical-align: mi;
}
.shop .shopContent .shopItem .shopImg img[src] {
  visibility: visible;
}
.shop .shopContent .shopItem .shopDesc {
  -ms-flex: 1 1;
      flex: 1 1;
}
.shop .shopContent .shopItem .shopDesc .shopName {
  margin-bottom: 0.2rem;
}
.shop .shopContent .shopItem .shopDesc .shopServer {
  margin-top: 0.2rem;
}
.shop .shopContent .shopItem .shopDesc .shopServer li {
  display: inline-block;
  font-size: 0.24rem;
  margin-right: 0.08rem;
}
.shop .shopContent .shopItem .shopDesc .shopServer li:last-child {
  margin: 0;
}



