.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100VW;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 0.24rem;
  height: 1rem;
  padding: 0rem 0.24rem;
  padding-top: 0.2rem;
  border-top: 0.01rem solid rgba(0, 0, 0, 0.3);
}
.footer div {
  display: inline-block;
  padding: 0rem 0.48rem;
  text-align: center;
}
.footer div .active {
  color: #11b9b2;
}
.footer div .iconfont {
  font-size: 0.36rem;
  padding-right: 0.1rem;
}
.footer div span {
  display: block;
}



