.cart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  height: 0.96rem;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  background-color: #141d27;
}
.cart .c-c {
  position: absolute;
  left: 0.24rem;
  bottom: 0.16rem;
  width: 1.16rem;
  height: 1.16rem;
  text-align: center;
  line-height: 1.24rem;
  background-color: #141d27;
  border-radius: 50%;
}
.cart .c-c .c-c-c {
  display: inline-block;
  width: 0.88rem;
  height: 0.88rem;
  line-height: 0.88rem;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.4);
}
.cart .c-c .c_c_count {
  display: inline-block;
  width: 0.88rem;
  height: 0.88rem;
  line-height: 0.88rem;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.4);
  color: #ffffff;
  background-color: #00a0dc;
}
.cart .c-c .count {
  position: absolute;
  top: 0;
  font-weight: 700;
  color: #ffffff;
  background-color: #f01414;
  left: 0.88rem;
  width: 0.48rem;
  height: 0.32rem;
  border-radius: 0.12rem 0.12rem;
  font-size: 0.18rem;
  line-height: 0.32rem;
  -webkit-box-shadow: 0rem 0.04rem 0.08rem 0rem rgba(0, 0, 0, 0.4);
          box-shadow: 0rem 0.04rem 0.08rem 0rem rgba(0, 0, 0, 0.4);
}
.cart .c-left {
  display: -ms-flexbox;
  display: flex;
}
.cart .c-left .c-price {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
  line-height: 0.48rem;
  width: 3.8rem;
  padding-right: 0.24rem;
  margin: 0.24rem 0rem;
  border-right: 0.02rem rgba(255, 255, 255, 0.1) solid;
}
.cart .c-left .c-price .c-p-prize {
  display: inline-block;
  color: #ffffff;
  font-weight: 700;
  font-size: 0.32rem;
}
.cart .c-left .c-info {
  line-height: 0.96rem;
  padding-left: 0.24rem;
  padding-right: 0.2rem;
}
.cart .c-left .c-info .c-i-info {
  display: inline-block;
  color: rgba(255, 255, 255, 0.4);
  font-size: 0.16rem;
}
.cart .c-right {
  width: 1.5rem;
  height: 0.96rem;
  font-size: 0.24rem;
  text-align: center;
  float: right;
  line-height: 0.96rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.4);
}
.cart .c-right.payment {
  color: #ffffff;
  background-color: #00a0dc;
}



