.lr {
  font-size: 0.28rem;
  width: 100vw;
  min-height: 100vh;
  background-color: #F5F5F5;
}
.lr .lr_main {
  padding-top: 1.2rem;
}
.lr .lr_main .f_warpper {
  line-height: 1rem;
  height: 1rem;
  padding: 0rem 0.2rem;
  border-bottom: 0.01rem solid #F5F5F5;
  color: #666;
  font-weight: 600;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
}
.lr .lr_main .f_warpper input {
  line-height: 0.8rem;
  padding-left: 0.15rem;
  caret-color: #ccc;
}
.lr .lr_main .f_warpper .s_code {
  width: 1.98rem;
  height: 0.4rem;
  border-right: 0.04rem solid #3dcc30;
}
.lr .lr_main .f_warpper .togg_warpper {
  display: inline-block;
  position: relative;
  text-align: right;
  line-height: 0.3rem;
}
.lr .lr_main .f_warpper .togg_warpper .show_hidden {
  width: 1.1rem;
  height: 0.3rem;
  border-radius: 0.2rem;
  color: #fff;
  display: inline-block;
  background-color: #4cd964;
}
.lr .lr_main .f_warpper .togg_warpper .show_hidden.showPs {
  background-color: #ccc;
}
.lr .lr_main .f_warpper .togg_warpper .toggle {
  position: absolute;
  display: inline-block;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  background-color: #f1f1f1;
  width: 0.5rem;
  height: 0.5rem;
  top: -0.1rem;
  left: 0.6rem;
}
.lr .lr_main .f_warpper .togg_warpper .toggle.psHidden {
  left: -0.04rem;
}
.lr .lr_main .f_warpper .codeImg {
  line-height: 1.2rem;
  padding-left: 0.2rem;
  display: inline-block;
  vertical-align: top;
}
.lr .lr_main .f_warpper .codeImg img {
  width: 1.2rem;
  height: 0.5rem;
  margin-top: 0.3rem;
  display: inline-block;
}
.lr .lr_main .f_warpper .codeImg .codeText {
  width: 1.2rem;
  height: 0.5rem;
  margin-left: 0.2rem;
  display: inline-block;
}
.lr .lr_main .f_warpper .codeImg .codeText .toggle_img {
  vertical-align: top;
  line-height: 0.8rem;
  font-size: 0.28rem;
  display: inline-block;
  color: #3b95e9;
  font-weight: 200;
}
.lr .lr_main .tip {
  color: red;
  padding: 0.1rem 0.15rem;
  font-size: 0.18rem;
  letter-spacing: 0.02rem;
}
.lr .lr_main .sub {
  margin-top: 0.1rem;
  padding: 0rem 0.15rem;
}
.lr .lr_main .sub .submit {
  width: 6rem;
  height: 0.8rem;
  font-size: 0.28rem;
  border-radius: 0.08rem;
  display: inline-block;
  color: #fff;
  background-color: #4cd964;
}
.lr .lr_main .sub .disabled {
  width: 6rem;
  height: 0.8rem;
  font-size: 0.28rem;
  border-radius: 0.08rem;
  display: inline-block;
  color: #fff;
  background-color: #4cd964;
  background-color: #c4c4c4;
  color: #999;
}
.lr .lr_main .resetPass {
  margin-top: 0.4rem;
  padding: 0rem 0.15rem;
  letter-spacing: 0.02rem;
  text-align: right;
}
.lr .lr_main .resetPass .reset {
  color: #3b95e9;
}



