.collect {
  position: fixed;
  left: 0;
  right: 0;
  top: 0.8rem;
}
.collect .c_nav {
  display: -ms-flexbox;
  display: flex;
}
.collect .c_nav .n_bar {
  -ms-flex: 1 1;
      flex: 1 1;
  display: inline-block;
  text-align: center;
  line-height: 0.8rem;
}
.collect .c_nav .n_bar a {
  display: block;
  font-size: 0.3rem;
}
.collect .c_nav .n_bar a.active {
  color: #3190e8;
  position: relative;
}
.collect .c_nav .n_bar a.active:before {
  content: '';
  position: absolute;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  border-bottom: 0.02rem solid #3190e8;
  left: 0%;
  right: 0%;
  bottom: 0px;
}
.collect .c_nav .n_bar a.active:active:before {
  left: 51%;
  right: 56%;
}
.collect .c_nav:before {
  content: '';
  position: absolute;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  border-bottom: 0.02rem solid #ccc;
  left: 0%;
  right: 0%;
  bottom: 0px;
}



